.Projects {
    .content {
        flex-grow: 1;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        padding: 1.5rem;
    }
    .header-text {
        font-size: 4rem;
        letter-spacing: .2rem;
        font-family: 'Montserrat', sans-serif;
        margin-bottom: 2rem;
        align-self: center;
    }
    .project-selector-button {
        background-color: white;
        border-radius: 0.5rem;
        font-size: 1.4rem;
        padding: .5rem 2.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-bottom: 0.6rem;
        color: darkcyan;
        font-weight: 700;
        font-family: 'Montserrat', sans-serif;
        box-shadow: none;
        border: 0.2rem solid darkcyan;
        transition: all 0.3s;
        cursor: pointer;
        &:hover {
            background-color: darkcyan;
            color: white;
            transition: all 0.3s;
        }
    }
    .current-project {
        border: 2px solid rgba(0, 0, 0, 0.5);
        border-radius: 0.2rem;
        flex-grow: 1;
        width: 100%;
        margin: .5rem 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
}
