@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,h1,h2,h3,h4,h5,h6,a,p,span,em,small,strong,sub,sup,mark,del,ins,strike,abbr,dfn,blockquote,q,cite,code,pre,ol,ul,li,dl,dt,dd,div,section,article,main,aside,nav,header,hgroup,footer,img,figure,figcaption,address,time,audio,video,canvas,iframe,details,summary,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0}html{font-size:62.5%;margin:0}body{font-size:1.6rem;line-height:1.4}*{font-family:inherit;font-size:inherit;line-height:inherit}a,a:visited{color:inherit}article,aside,footer,header,nav,section,main{display:block}*{box-sizing:border-box}*:before,*:after{box-sizing:inherit}table{border-collapse:collapse;border-spacing:0}ol,ul{list-style:none}img,video{max-width:100%}img{border-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:"";content:none}[hidden]{display:none !important}[disabled]{cursor:not-allowed}:focus:not(:focus-visible){outline:none}.sr-only{position:absolute;clip:rect(1px, 1px, 1px, 1px);left:-9999px;top:-9999px}html{height:100%}body{min-height:100vh}#root{min-height:100vh;display:flex;flex-direction:column}.container{position:relative;display:flex;flex-direction:column;flex-grow:1}.page{position:absolute;width:100%;height:100%;display:flex;flex-direction:column;flex-grow:1}.page-enter{opacity:0;transform:scale(1.1)}.page-enter-active{opacity:1;transform:scale(1);transition:opacity 300ms,transform 300ms}.page-exit{opacity:1;transform:scale(1)}.page-exit-active{opacity:0;transform:scale(0.9);transition:opacity 300ms,transform 300ms}
.navbar{width:100%;height:4rem;box-shadow:0px 3px 3px rgba(120,120,120,.5)}.navbar__inner{display:flex;flex-direction:row;align-items:center;justify-content:flex-end;padding:0 5rem;width:100%;height:100%}.navbar__item{margin-right:3rem}.navbar__item.on{color:pink !important}
.Home .content{flex-grow:1;max-width:56rem;flex-grow:1;align-self:center;display:flex;flex-direction:column;padding:4rem 0}.Home .header-text{font-size:4rem;letter-spacing:.2rem;font-family:"Montserrat",sans-serif;margin-bottom:3rem;align-self:center}.Home .welcome-text{margin-bottom:3rem}.Home .headshot{--size: 30rem;width:var(--size);height:var(--size);align-self:center}
.About .content{flex-grow:1;max-width:56rem;flex-grow:1;align-self:center;display:flex;flex-direction:column;padding:4rem 0}.About .header-text{font-size:4rem;letter-spacing:.2rem;font-family:"Montserrat",sans-serif;margin-bottom:3rem;align-self:center}.About .text{margin-bottom:4rem;font-family:"Montserrat",sans-serif}.About .subheader{align-self:center;margin-bottom:2rem;font-family:"Montserrat",sans-serif}.About .image{--size: 30rem;width:var(--size);height:var(--size);align-self:center;margin-bottom:4rem;box-shadow:0px 0px 5px rgba(150,150,150,.7)}
.Projects .content{flex-grow:1;display:flex;align-items:flex-start;flex-direction:column;padding:1.5rem}.Projects .header-text{font-size:4rem;letter-spacing:.2rem;font-family:"Montserrat",sans-serif;margin-bottom:2rem;align-self:center}.Projects .project-selector-button{background-color:#fff;border-radius:.5rem;font-size:1.4rem;padding:.5rem 2.2rem;display:flex;align-items:center;justify-content:center;text-align:center;padding-bottom:.6rem;color:#008b8b;font-weight:700;font-family:"Montserrat",sans-serif;box-shadow:none;border:.2rem solid #008b8b;transition:all .3s;cursor:pointer}.Projects .project-selector-button:hover{background-color:#008b8b;color:#fff;transition:all .3s}.Projects .current-project{border:2px solid rgba(0,0,0,.5);border-radius:.2rem;flex-grow:1;width:100%;margin:.5rem 0;display:flex;flex-direction:column;align-items:flex-start}
.DrumKit-outer{flex-grow:1;width:100%;display:flex;justify-content:center;background-size:cover;background-position:center;background-repeat:no-repeat;position:relative;overflow:hidden;padding:1rem;box-sizing:border-box;max-height:60rem}.DrumKit-outer .drum-button{--size: 7.5rem;flex-grow:1;flex-shrink:0;display:flex;flex-direction:column;align-items:center;justify-content:center;width:var(--size);height:var(--size);color:#fff;border-radius:50%;font-weight:bold;text-transform:uppercase;overflow:hidden;-webkit-user-select:none;-ms-user-select:none;user-select:none}.DrumKit-outer .drum-button:focus{outline:none}.DrumKit-outer .pressed{background-color:#c8c8c8 !important}.DrumKit-outer .drum-name{text-overflow:clip}.DrumKit-outer .animated{transition:transform cubic-bezier(0.57, 2.06, 0.25, 1) .2s}.DrumKit-outer .animated.animating{transition:transform cubic-bezier(0.57, 2.06, 0.25, 1) .2s}.DrumKit-outer .animated#Anim-TomMid{transform:scaleX(1) scaleY(1) rotate(0deg);transform-origin:50% 25%}.DrumKit-outer .animated#Anim-TomMid.animating{transform:scaleX(1) scaleY(0.9) rotate(-3deg)}.DrumKit-outer .animated#Anim-TomHigh{transform:scaleX(1) scaleY(1) rotate(0deg);transform-origin:50% 25%}.DrumKit-outer .animated#Anim-TomHigh.animating{transform:scaleX(1) scaleY(0.9) rotate(3deg)}.DrumKit-outer .animated#Anim-TomFloor{transform-origin:25% 75%;transform:scaleX(1) scaleY(1)}.DrumKit-outer .animated#Anim-TomFloor.animating{transform:scaleX(1.05) scaleY(0.9)}.DrumKit-outer .animated#Anim-HiHat{transition:transform cubic-bezier(0.57, 3.79, 0.46, 0.15) .2s;transform-origin:85% 35%;transform:rotate(0deg)}.DrumKit-outer .animated#Anim-HiHat.animating{transform:rotate(-5deg)}.DrumKit-outer .animated#Anim-Ride{transition:transform cubic-bezier(0.28, 4.5, 0.85, -0.96) .3s;transform-origin:22% 10%;transform:rotate(0deg)}.DrumKit-outer .animated#Anim-Ride.animating{transform:rotate(5deg)}.DrumKit-outer .animated#Anim-Kick{transform-origin:50% 100%;transform:scaleX(1) scaleY(1)}.DrumKit-outer .animated#Anim-Kick.animating{transform:scaleX(1.1) scaleY(0.98)}.DrumKit-outer .animated#Anim-Snare{transform-origin:75% 55%;transform:scaleX(1) scaleY(1)}.DrumKit-outer .animated#Anim-Snare.animating{transform:scaleX(1.1) scaleY(0.9)}.sequencer-row{display:flex;align-items:center;margin-bottom:.5rem}.sequencer-row .sequencer-img{height:3rem;width:3rem;border-radius:.5rem;margin-right:.5rem;border:1px solid rgba(250,250,250,.5)}.sequencer-btn{background:rgba(0,0,0,.5);min-width:3rem;min-height:3rem;border-radius:.5rem;margin-left:.2rem;border:1px solid rgba(250,250,250,.5);position:relative;cursor:pointer;transition:transform .2s background .2s;transform:scale(1)}.sequencer-btn:hover{transition:transform .2s;transform:scale(1.05)}.sequencer-btn.active{transition:background .2s;background:rgba(39,150,5,.3)}.sequencer-btn::after{content:"";position:absolute;inset:0;background:rgba(0,0,0,0);transition:background .2s}.sequencer-btn.glowing::after{border:1px solid gold;border-radius:.5rem}.drum-project-container{width:100%;height:100%;display:flex;flex-direction:row;background-color:rgba(140,140,200,.2)}.sequencer-container{background-color:rgba(0,0,0,.4);padding:1rem;border-radius:.5rem;margin:1rem 0 0 1rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;flex-shrink:0}.sequencer-options{display:flex;align-items:center;justify-content:flex-end;padding:.5rem 0 0}.sequencer-options .play-button{background-color:rgba(250,250,250,.5);color:#fff;cursor:pointer;width:4rem;height:3rem;display:flex;align-items:center;justify-content:center;border-radius:.5rem;box-shadow:unset;border:unset;margin-right:2rem}.sequencer-options .play-button:hover{box-shadow:1px 1px 2px rgba(0,0,0,.2)}.sequencer-options .play-button .play-button-img{width:2rem;height:2rem;object-position:center;object-fit:contain}.sequencer-options .bpm-options{display:flex;align-items:center}.sequencer-options .bpm-options button{border-radius:.5rem;width:3rem;height:3rem;background-color:rgba(250,250,250,.5);box-shadow:unset;border:unset;color:#000;font-size:2.2rem;font-weight:bold;display:flex;align-items:center;justify-content:center;line-height:3rem;cursor:pointer}.sequencer-options .bpm-options button:hover{box-shadow:1px 1px 2px rgba(0,0,0,.2)}.sequencer-options .bpm-options span{font-size:1.6rem;font-weight:bold;margin:0 .2rem}.sequencer-options .bpm-options span::after{content:"BPM";margin:0 .1rem;font-size:1rem}
